<template>
  <transition name="fade">
    <section class="match-wrapper">
      <MyHeader :index="false" />
      <Subcontent ref="subcontent" :title-show="false" :title="title" :margin="0">
        <div class="match-box">
          <section class="status-box">
            <div class="avatar-box">
              <div class="avatar left">
                <div class="photo"><img :src="img" /></div>
              </div>
              <p class="nickname left">dddd</p>
            </div>
            <div class="score-box">
              <div class="score">
                <span :style="{'width': score + '%'}" class="score-inner"></span>
              </div>
              <div class="number">
                <span>0</span>
                <span>150</span>
              </div>
            </div>
            <div class="time">
              <svg class="svg" width="100%" height="100%" viewbox="0 0 162 162"
                   version="1.1" xmlns="http://www.w3.org/2000/svg">
                  <circle cx="50%" cy="50%" r="67"
                          stroke="#d3d3d3" fill="none"
                          stroke-width="27" stroke-linecap="round"
                  />
                  <circle cx="50%" cy="50%" r="67"
                          stroke="#ff776c" fill="none"
                          stroke-width="15" transform="matrix(0,-1,1,0,0,162)"
                          :style="{'stroke-dasharray': circlePercent(75, 40)}"
                  />
              </svg>
              <div class="time-inner">4</div>
            </div>
            <div class="score-box">
              <div class="score rotate">
                <span :style="{'width': score + '%'}" class="score-inner"></span>
              </div>
              <div class="number">
                <span>150</span>
                <span>0</span>
              </div>
            </div>
            <div class="avatar-box">
              <div class="avatar right">
                <div class="photo"><img :src="img" /></div>
              </div>
              <p class="nickname right">dwdwdw</p>
            </div>
          </section>
          <section class="test-box">
            <div class="test">
              <h4 class="test-title">第8题</h4>
              <p class="test-text">大会强调，围绕回答新时代坚持和发展什么样的中国特色社会主义、怎样坚持和发展中国特色社会主义这个重大时代课题，
                我们党以全新的视野深化对共产党执政规律、________规律、人类社会发展规律的认识，进行艰辛理论探索，取得重大理论创新成果。</p>
            </div>
            <ul class="choose-list">
              <li :class="{'active': true}" class="choose-item">
                <span class="circle"></span>
                <span class="text">两个一百年”奋斗目标、实现中华民族伟大复兴的中国梦</span>
              </li>
              <li class="choose-item">
                <span class="circle"></span>
                <span class="text">社会主义建设</span>
              </li>
              <li class="choose-item">
                <span class="circle"></span>
                <span class="text">社会主义政治</span>
              </li>
              <li class="choose-item">
                <span class="circle"></span>
                <span class="text">社会主义经济</span>
              </li>
            </ul>
          </section>
        </div>
      </Subcontent>
    </section>
  </transition>
</template>

<script type="text/ecmascript-6">
  import MyHeader from '@/components/Header/Header'
  import Subcontent from '@/base/Subcontent/Subcontent'
  import io from 'io'

  export default {
    data () {
      return {
        title: '123',
        img: 'http://cdn.duitang.com/uploads/item/201405/27/20140527175157_rnKBA.thumb.600_0.jpeg',
        score: 30
      }
    },
    methods: {
      // 环状进度
      circlePercent: function (radius, percent) {
          return Math.PI * 2 * radius * percent / 100 + ' ' + Math.PI * 2 * radius * (1 - percent / 100);
      }
    },
    created () {
      console.log(io);
      // 连接服务端
      let serverdomain = 'wei.wiseljz.com';
      let socket = io('http://' + serverdomain + ':2120');
      // 连接后登录
      socket.on('connect', function(){
          console.log('connect')
          // console.log(uid);
          // socket.emit('login', uid);
      });
      // 后端推送来消息时
      socket.on('new_msg', function(msg){
          console.log('new_msg')
          console.log(msg)
      });
      // 后端推送来在线数据时
      socket.on('update_online_count', function(online_stat){
          console.log('update_online_count')
          console.log(online_stat)
      });
    },
    components: {
      MyHeader,
      Subcontent
    }
  }
</script>

<style rel="stylesheet/less" lang="less" scoped>
  @import '../../assets/less/variable';
  @import '../../assets/less/mixin';

  .match-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 110;
    width: 1920px;
    height: 1080px;
    background: url('../../assets/img/bg.jpg') no-repeat;
    overflow: hidden;
    &.fade-enter-active, &.fade-leave-active {
    transition: opacity .5s;
    }
    &.fade-enter, &.fade-leave-to {
      opacity: 0;
    }
    .match-box {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 100%;
      overflow: hidden;
    }
    .status-box {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 26px;
      .avatar {
        display: flex;
        align-items: center;
        width: 283px;
        height: 177px;
        box-sizing: border-box;
        &.left {
          justify-content: flex-end;
          padding-right: 12px;
          background: #f08152;
          border-top-right-radius: 1000px;
          border-bottom-right-radius: 1000px;
        }
        &.right {
          justify-content: flex-start;
          padding-left: 12px;
          background: #fda631;
          border-top-left-radius: 1000px;
          border-bottom-left-radius: 1000px;
        }
        .photo {
          width: 142px;
          height: 142px;
          border: solid 8px #fff;
          border-radius: 50%;
          overflow: hidden;
          & > img {
            display: block;
            width: 100%;
            height: 100%;
          }
        }
      }
      .nickname {
        color: #717171;
        font-size: 30px;
        line-height: 50px;
        box-sizing: border-box;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        &.left {
          text-align: right;
          padding-right: 48px;
        }
        &.right {
          text-align: left;
          padding-left: 48px;
        }
      }
      .score {
        width: 430px;
        height: 20px;
        background-color: #c4c4c4;
        border: solid 7px #ffe2c7;
        border-radius: 100px;
        &.rotate {
          transform: rotate(180deg);
        }
        .score-inner {
          display: block;
          height: 100%;
          background-color: #feaa00;
	        border-radius: 1000px;
        }
      }
      .number {
        display: flex;
        justify-content: space-between;
        color: #e88d3e;
        font-size: 30px;
        line-height: 30px;
        margin-top: 5px;
      }
      .time {
        width: 162px;
        height: 162px;
        position: relative;
        .time-inner {
          width: 60px;
          height: 60px;
          line-height: 60px;
          text-align: center;
          color: #717171;
          font-size: 56px;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }
    }
    .test-box {
      flex: 1;
      display: flex;
      box-sizing: border-box;
      padding-top: 50px;
      .test {
        width: 800px;
        box-sizing: border-box;
        padding-bottom: 20px;
        margin: 0 125px;
        overflow: auto;
        .scroll-bar(12px, rgba(136, 136, 136, 0.22));
        .test-title {
          color: #c78300;
          font-size: 42px;
          line-height: 90px;
        }
        .test-text {
          color: #565656;
          font-size: 28px;
          line-height: 50px;
        }
      }
      .choose-item {
        display: flex;
        align-items: center;
        max-width: 600px;
        height: 93px;
        color: #fff;
        padding: 0 32px;
        margin-bottom: 21px;
        background: #bbb;
        box-shadow: 0px 3px 3px 0px rgba(116, 116, 116, 0.35);
        border-radius: 20px;
        cursor: pointer;
        &:last-child {
          margin-bottom: 0;
        }
        &.active {
          background: #92d500;
          .circle {
            border: 6px solid #fff;
          }
        }
        .circle {
          width: 32px;
          height: 32px;
          box-sizing: border-box;
          margin-right: 30px;
          border: 6px solid #bbb;
          border-radius: 50%;
        }
        .text {
          flex: 1;
          font-size: 32px;
        }
      }
    }
  }
</style>


