<template>
  <div class="section-wrapper">
    <Subtitle :title="subtitle" />
    <div class="rank">
      <ul class="rank-list">
        <li v-for="(item, index) in dataList" class="rank-item">
          <span class="rank-index">
            <img v-if="index <= 2" :src="rankImg(index)" />
            <span v-else>{{index + 1}}.</span>
          </span>
          <span class="rank-avatar">
            <img :src="item.avatar" />
          </span>
          <span class="rank-name">{{item.name}}</span>
          <span class="rank-score">{{item.score}}</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Subtitle from '@/base/Subtitle/Subtitle'

  export default {
    data () {
      return {
        subtitle: '测试结果排名',
        dataList: [
          {
            name: '卜惠文',
            score: 100,
            avatar: require('./EdSheeran.jpg')
          },
          {
            name: '韩鹤龄',
            score: 95,
            avatar: require('./EdSheeran.jpg')
          },
          {
            name: '杭秀琴',
            score: 90,
            avatar: require('./EdSheeran.jpg')
          },
          {
            name: '李清颢',
            score: 85,
            avatar: require('./EdSheeran.jpg')
          },
          {
            name: '刘国兴',
            score: 80,
            avatar: require('./EdSheeran.jpg')
          },
          {
            name: '郑志扬',
            score: 75,
            avatar: require('./EdSheeran.jpg')
          }
        ]
      }
    },
    methods: {
      rankImg (rank) {
        return `/static/imgs/${rank+1}.png`
      }
    },
    components: {
      Subtitle
    }
  }  
</script>

<style rel="stylesheet/less" lang="less" scoped>
  @import '../../assets/less/variable';
  @import '../../assets/less/mixin';

  .section-wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
     background: #FFF;
    .rank {
      flex: 1;
      box-sizing: border-box;
      padding: 0 30px;
      margin: 0 10px;
      overflow: auto;
      .scroll-bar(12px, rgba(136, 136, 136, 0.22));
    }
    .rank-list {
      display: flex;
      flex-wrap: wrap;
      .rank-item {
        display: flex;
        align-items: center;
        width: 50%;
        padding: 27px 0;
        border-bottom: 2px dashed @colorBorder;
        .rank-index {
          width: 100px;
          color: #5e5e5e;
          font-size: 30px;
          text-align: center;
        }
        .rank-avatar {
          width: 58px;
          height: 58px;
          margin: 0 35px 0 7px;
          border-radius: 50%;
          overflow: hidden;
          & > img {
            display: block;
            width: 100%;
            height: 100%;
          }
        }
        .rank-name {
          width: 150px;
          color: #5e5e5e;
          font-size: 30px;
          margin-right: 50px;
        }
        .rank-score {
          width: 100px;
          color: #ebac00;
          font-size: 40px;
          font-family: 'HYk2gj';
          text-align: center;
        }
      }
    }
  }
</style>


