<template>
  <transition name="fade">
    <section class="mode-wrapper">
      <MyHeader :index="false" />
      <Subcontent :title="title">
        <div class="mode-box">
          <div class="mode-select">
            <p class="mdoe-select-title">学习结束，请选择测评模式</p>
            <ul class="mdoe-select-list">
              <li @click="changeType(1)" 
                  :class="{'active': type === 1}" 
                  class="mdoe-select-item">本机测试
              </li>
              <!-- <li @click="changeType(2)" 
                  :class="{'active': type === 2}" 
                  class="mdoe-select-item">单人模式
              </li> -->
              <li @click="changeType(3)" 
                  :class="{'active': type === 3}" 
                  class="mdoe-select-item">对战模式
              </li>
            </ul>
          </div>
          <div class="mode-entrance">
            <div @click="localTest" v-if="type === 1" class="local-test">点击前往本机测试</div>
            <div @click="ewmTest" v-if="type === 2 || type === 3" class="ewm-box">
              <div class="ewm">
                <img src="./logo.png" />
              </div>
              <p class="text">手机扫描二维码进行测试</p>
            </div>
          </div>
        </div>
      </Subcontent>
    </section>
  </transition>
</template>

<script type="text/ecmascript-6">
  import MyHeader from '@/components/Header/Header'
  import Subcontent from '@/base/Subcontent/Subcontent'

  export default {
    props: {
      id: {
        type: [Number, String],
        default: 0
      }
    },
    data () {
      return {
         title: '知识测评',
         type: 1
      }
    },
    methods: {
      changeType (type) {
        this.type = type
      },
      localTest () {
        this.$router.push({
          path: `/study/${this.id}/test`
        })
      },
      ewmTest () {
        this.$router.push({
          path: `/study/${this.id}/match`
        })
      }
    },
    created () {
      // console.log(this.id);
    },
    components: {
      MyHeader,
      Subcontent
    }
  }
</script>

<style rel="stylesheet/less" lang="less" scoped>
  @import '../../assets/less/variable';

  .mode-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 110;
    width: 1920px;
    height: 1080px;
    background: url('../../assets/img/bg.jpg') no-repeat;
    overflow: hidden;
    &.fade-enter-active, &.fade-leave-active {
    transition: opacity .5s;
    }
    &.fade-enter, &.fade-leave-to {
      opacity: 0;
    }
    .mode-box {
      display: flex;
      width: 100%;
      height: 100%;
    }
    .mode-select {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 1050px;
      .mdoe-select-title {
        color: @colorText;
        font-size: 40px;
      }
      .mdoe-select-list {
        margin-top: 70px;
      }
      .mdoe-select-item {
        width: 347px;
        height: 112px;
        line-height: 112px;
        text-align: center;
        color: #fff;
        font-size: 44px;
        margin-bottom: 54px;
        background-color: #d36f3a;
	      box-shadow: 0px 10px 0px 0px #b74d09;
        border-radius: 14px;
        cursor: pointer;
        &:last-child {
          margin-bottom: 0;
        }
        &.active {
          background-color: #f78700;
          box-shadow: 0px 10px 0px 0px #da6e00;
        }
      }
    }
    .mode-entrance {
      flex: 1;
      display: flex;
      align-items: center;
      .ewm {
        width: 290px;
        height: 290px;
        margin: 0 auto;
        border: 14px solid #f7b827;
        & > img {
          display: block;
          width: 100%;
          height: 100%;
        }
      }
      .text {
        color: #d17400;
        font-size: 34px;
        line-height: 75px;
      }
      .local-test {
        line-height: 100px;
        color: #fff;
        font-size: 40px;
        padding: 0 20px;
        background-color: #ec7036;
        border-radius: 10px;
        cursor: pointer;
      }
    }
  }
</style>


